<template>
  <div class="center">
    <div class="center_breadcrumb">
      <crumb style="padding: 0 5px;">
        <div class="new--btn" slot="right" @click="openAdd">+ 新建</div>
      </crumb>
    </div>
    <div class="center_content">
      <div class="center_content_search">
        <div class="center_content_search_left">
          <div class="search-input-suffix">
            <label>分类名称：</label>
            <el-input
              class="search"
              @keyup.enter.native="search"
              v-model="page.keywords"
              placeholder="请输入分类名称"
            >
            </el-input>
            <!-- <el-button  @click="search" type="primary">搜索</el-button> -->
          </div>
          <div class="search-input-suffix" style="margin: 0 0 0 20px">
            <div class="search--btn" @click="search">搜索</div>
          </div>
        </div>
      </div>
      <div class="table-container">
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column prop label="编号" align="center" width="100">
            <template slot-scope="scope">{{scope.$index +1}}</template>
          </el-table-column>
          <el-table-column prop="name" label="分类名称"></el-table-column>
          <el-table-column prop="parentVal" label="上级分类"></el-table-column>
          <el-table-column prop="description" label="分类描述"></el-table-column>
          <el-table-column label="操作" width="234">
                <template slot-scope="scope">                  
                    <div class="operate-button distance">
                        <SvgIcon icon-class="iconbianji1"></SvgIcon> 
                        <el-button
                        @click.native.prevent="editRow(scope.row)"
                        type="text"
                        size="small">
                        编辑
                        </el-button>
                    </div>
                    <div class="operate-button">
                        <SvgIcon icon-class="iconshanchu"></SvgIcon>
                        <el-button
                        @click.native.prevent="deleteRow(scope.row)"
                        type="text"
                        size="small"
                        style="color: #ff5858">
                        删除
                        </el-button>
                    </div>                    
                </template>
          </el-table-column>
        </el-table>
        <div class="center_pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            background
            :current-page="page.page"
            :page-sizes="[15,30,40,50]"
            :page-size="page.rows"
            layout="prev, pager, next, jumper, sizes, total"
            :total="data.total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 新增编辑 -->
    <el-dialog
      :title="dialogTitle"
      :visible.sync="userDialogFormVisible"
      :before-close="handleDialogClose"
      class="set-el-dialog"
      :close-on-click-modal="false"
    >
      <el-form :model="form" :rules="rules" ref="form" label-width="120px">
        <el-form-item label="分类名称" prop="name">
          <el-input v-model="form.name" placeholder></el-input>
        </el-form-item>
        <el-form-item label="分类描述" prop="description">
          <el-input type="textarea" placeholder v-model="form.description"></el-input>
        </el-form-item>
        <el-form-item label="上级分类" prop="parentId">
          <el-select v-model="form.parentId" filterable placeholder="请选择">
            <el-option
              v-for="item in parentOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel('form')">取 消</el-button>
        <el-button type="primary" @click="create('form')" :disabled="isSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import trainingProgramApi from "@/api/trainingProgramApi";
export default {
  name: "CourseClassification",
  data() {
    return {
      pageTitle: "课程分类",
      data: {},
      isAdd: true,
      isSubmit:false,
      tableData: [],
      userDialogFormVisible: false,
      parentOptions: [],
      form: {
        name: "",
        description: "",
        parentId: "0",
        id: undefined
      },
      rules: {
        name: [
          {
            required: true,
            message: "请输入分类名称",
            trigger: "blur"
          }
        ],
        parentId: [
          {
            required: true,
            message: "请选择上级分类",
            trigger: "blur"
          }
        ]
      },
      page: {
        keywords: "",
        page: 1,
        rows: 15
      }
    };
  },
  created() {},
  mounted() {
    this.initData();
    this.courseClassificationAll();
  },
  computed: {
    dialogTitle() {
      return this.isAdd ? "添加课程分类" : "编辑课程分类";
    }
  },
  methods: {
    initData() {
      const url = trainingProgramApi.courseClassificationList;
      this.$post(url, this.page)
        .then(res => {
          if (res.status === 1) {
            this.data = res;
            this.tableData = res.data;
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    courseClassificationAll() {
      const url = trainingProgramApi.courseClassificationAll;
      this.$get(url)
        .then(res => {
          if (res.status === 1) {
            const allArr = {
              id: "0",
              name: "顶层分类"
            };
            res.data.unshift(allArr);
            this.parentOptions = res.data;
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    headInfo(item) {
      this.pageTitle = item[1];
    },
    search() {
      this.resetTemp();
      this.initData();
    },
    handleSizeChange(val) {
      this.page.rows = val;
      this.initData();
    },
    handleCurrentChange(obj) {
      this.page.page = obj;
      this.initData();
    },
    openAdd() {
      this.courseClassificationAll();
      this.userDialogFormVisible = true;
      this.form = {
        name: "",
        description: "",
        parentId: "",
        id: undefined
      };
      this.isAdd = true;
      this.isSubmit=false;
      this.$nextTick(() => {
        this.$refs["form"].resetFields();
      });
    },
    deleteRow(row) {
      this.$confirm("确定删除该分类?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.userDel(row.id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    editRow(row) {
      this.courseClassificationAll();
      const obj = {
        name: row.name,
        description: row.description,
        parentId: row.parentId,
        id: row.id
      };
      this.form = obj;
      this.userDialogFormVisible = true;
      this.isAdd = false;
      this.isSubmit=false;
    },
    create(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
           this.isSubmit=true;
          if (this.isAdd) {
            this.userAdd();
          } else {
            this.userEdit();
          }
        }
      });
    },
    cancel(formName) {
      this.userDialogFormVisible = false;
      this.$refs[formName].resetFields();
    },
    handleDialogClose() {
      this.$refs["form"].resetFields();
      this.userDialogFormVisible = false;
    },
    userAdd() {
      const url = trainingProgramApi.courseClassificationAdd;
      this.$post(url, this.form)
        .then(res => {
          if (res.status === 1) {
             this.isSubmit=true;
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.resetTemp();
            this.initData();
          } else {
             this.isSubmit=false;
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
           this.isSubmit=false;
          console.log(err);
        });
    },
    userDel(id) {
      const url = trainingProgramApi.courseClassificationDel({
        id: id
      });
      this.$deletes(url)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    userEdit() {
      const url = trainingProgramApi.courseClassificationEdit;
      this.$put(url, this.form)
        .then(res => {
          if (res.status === 1) {
             this.isSubmit=true;
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.initData();
          } else {
             this.isSubmit=false;
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
           this.isSubmit=false;
          console.log(err);
        });
    },
    resetTemp() {
      this.page.page = 1;
      this.page.rows = 15;
    }
  }
};
</script>

<style lang='scss'>
@import "../../styles/center.scss";
</style>



